import { Component } from '@angular/core';
import moment from 'moment'
import count from "../assets/eggs.json";

import { KIPPEN } from '../kippen'


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  kippen = KIPPEN;
  daysAlive : number;
  eggs = count.eggs;


  constructor() { }

  ngOnInit(){
    this.daysAlive = this.calcDaysAlive(this.kippen[0].DOB);
  }

  calcDaysAlive(DOB: moment.Moment) : number{
    let daysAlive =  moment().diff(DOB, 'days');
    return daysAlive;
  }

}
